
.calendar-parent {
    height: 100%;    
    margin: 10px 10px 0px 5px;
    cursor: default;
}

/*     */
.fc {
    max-width: 100%;
}

/* toolbar container */
.fc .fc-toolbar.fc-header-toolbar {
    margin: 0;
    padding: 0 40px;
    background-color: #91B2FF;
    height: 63px;
    font-weight: 600;
    font-size: 12px;
    line-height: 29px;
    color: white;
    border-radius: 20px 20px 0px 0px;
}

/* toolbar  */
.fc .fc-button-primary {
    background-color: #4a90e2;
    border: none;
    margin: 10px;
    
}

/*      */
.fc .fc-view{ /*    FullCalendar   . */
    font-size: 0.75rem; /*   .    . */
    background-color: white;
}

/*   */
.fc-theme-standard th {
    width: 70px;
    height: 32px;
    padding-top: 3.5px;
    background: #e5edff;
    border: 1px solid #dddee0;
    font-weight: 500;
    font-size: 15px;
    line-height: 19px;
    color: #7b7b7b;
}

.fc .fc-today-button {
    background-color: #4a90e2; /* Replace #yourColor with your chosen hex color */
    color: white; /* Replace #yourTextColor with a contrasting color for the text */
    border-color: #98ABEE; /* Replace #yourBorderColor with your chosen hex color for the border */
    padding: 8px 16px; /* Increase padding for larger button size */
    
}

/*    */
.fc .fc-daygrid-day.fc-day-today {
    background-color: #FFF67E;
    color: #6895D2;
    
}

/*   */
.fc .fc-daygrid-day-frame {
    padding: 3px;
    
}

/*   ex) 2 */
.fc .fc-daygrid-day-top {
    flex-direction: row;
    margin-bottom: 3px;
}

/*    */
.fc-event {
    cursor: pointer;
    border-radius: 5px;
    font-weight: 500;
    font-size: 10px;
    
}

.fc-daygrid-day-events {
    overflow-y: auto; /* Enables vertical scrolling */
    max-height: 120px; /* Sets the maximum height for the event area */
    
}

  /* This ensures that the entire calendar row has a minimum height to accommodate events */
.fc-daygrid-day {
    height: 120px; /* Adjust this value based on your calendar's design needs */
    position: relative; /* Needed for absolute positioning of children */
}

/*       */
.fc .fc-day-sun a {
    color: red;
    /*     */
}

/*    */
.fc .fc-day-sat a {
    color: blue;
}